import { NgModule } from '@angular/core';
import { CommonModule, DatePipe } from '@angular/common';
import { PurchaseComponent } from './purchase.component';
import { PurchaseRoutingModule } from './purchase-routing.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import {MaterialModule} from '../../app.module';
import { HttpModule } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';
import { ItemsService } from '../items/items.service';
import { VendorService } from '../vendor/vendor.service';
import { NotifierModule, NotifierOptions } from 'angular-notifier';
import { NgbTimepickerModule } from '@ng-bootstrap/ng-bootstrap';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { DateInputsModule } from '@progress/kendo-angular-dateinputs';
import { MasterService } from '../master/master.service';
import { FoundryModule } from '../foundry.module';
import { StockConfirmationComponent } from './stock-confirmation/stock-confirmation.component';
const customNotifierOptions: NotifierOptions = {
  position: {
    horizontal: {
      position: 'middle',
      distance: 12
    },
    vertical: {
      position: 'top',
      distance: 50,
      gap: 10
    }
  },
  theme: 'material',
  behaviour: {
    autoHide: 1200,
    onClick: 'hide',
    onMouseover: 'pauseAutoHide',
    showDismissButton: true,
    stacking: 4
  },
  animations: {
    enabled: true,
    show: {
      preset: 'slide',
      speed: 300,
      easing: 'ease'
    },
    hide: {
      preset: 'fade',
      speed: 300,
      easing: 'ease',
      offset: 50
    },
    shift: {
      speed: 300,
      easing: 'ease'
    },
    overlap: 10
  }
};
@NgModule({
  declarations: [PurchaseComponent, StockConfirmationComponent],
  imports: [
    CommonModule, PurchaseRoutingModule, NgbModule, DateInputsModule, FormsModule, ReactiveFormsModule,FoundryModule,
    NgbTimepickerModule, MaterialModule, HttpModule,
    HttpClientModule, NotifierModule.withConfig(customNotifierOptions)
  ],
  providers: [ItemsService, VendorService, DatePipe,MasterService],
  entryComponents:[StockConfirmationComponent]
})
export class PurchaseModule { }
