import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { inject } from '@angular/core/testing';

@Component({
  selector: 'app-stock-confirmation',
  templateUrl: './stock-confirmation.component.html',
  styleUrls: ['./stock-confirmation.component.scss']
})
export class StockConfirmationComponent implements OnInit {
public date;
  constructor(public dialogRef:MatDialogRef<StockConfirmationComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any) { }

  ngOnInit() {
  }

  confirm() {
    this.date='YES';
    this.dialogRef.close(this.date);
  }
  
  cancel() {
    this.date = 'CANCEL';
    this.dialogRef.close(this.date);
  }
}
