import { Component, OnInit } from '@angular/core';
import { PurchaseService } from './purchase.service';
import { HttpClient } from '@angular/common/http';
import { FormControl, NgForm, Validators } from '@angular/forms';
import { Observable } from 'rxjs';
import { startWith, map } from 'rxjs/operators';
import { Vendor } from '../vendor/vendor';
import { Purchase } from './purchase';
import { ItemsService } from '../items/items.service';
import { Items } from '../items/items';
import { VendorService } from '../vendor/vendor.service';
import { ActivatedRoute } from '@angular/router';
import { NotifierService } from 'angular-notifier';
import { Router } from '@angular/router';
import { MasterService } from '../master/master.service';
import { Master } from '../master/master';
import { ItemTypeMaster } from '../master/item-type-master/item-type-master';
import { MatDialog } from '@angular/material';
import { StockConfirmationComponent } from './stock-confirmation/stock-confirmation.component';

@Component({
  selector: 'app-purchase',
  templateUrl: './purchase.component.html',
  styleUrls: ['./purchase.component.scss'],

})
export class PurchaseComponent implements OnInit {
  public value: Date = new Date();
  public vendorList: Vendor[];
  public itemList: Items[];
  public masterList: Master[];
  public myDate = new Date();
  public purchaseList: Purchase[];
  itemControl = new FormControl;
  filterItems: Observable<Items[]>;
  vendorControl = new FormControl;
  filterVendor: Observable<Vendor[]>;
  invoiceControl = new FormControl;
  filterInvoice: Observable<Purchase[]>;
  stockNoControl = new FormControl;
  filterStockNo: Observable<Purchase[]>;
  public itemTypeMasterList: ItemTypeMaster[];
  public time;
  public dateInvoice;
  public dateDelivery;
  public purchaseId;
  private notifier: NotifierService;
  public invoiceNo;
  public stockNo;
  public orderDate;
  public type;
  public vendorName;
  public unit;
  maxDate = new Date();

  purchase: Purchase = {
    _id: '',
    vendor: {
      _id: '', vendorName: '', vendorType: '', mobile: '', email: '', address: '', preferredType: '', createdBy: '',
      modifiedBy: ''
    },
    items: {
      _id: '', itemName: '', itemType: '', unit: '', gst: undefined, itemCategory: undefined,
      perQtyWeight: undefined, stockType: undefined, minWeightReq: undefined, createdBy: '', modifiedBy: '', status: ''
    },
    invoiceNo: undefined, invoiceDate: '', weight: undefined, transportCost: undefined,
    totalAmount: undefined, gstValue: undefined, stock: '', deliveryDate: undefined, deliveryTime: undefined,
    orderDate: undefined, orderTime: undefined,
    stockNo: '', createdBy: '', modifiedBy: ''
  };

  emailFormControl = new FormControl('', [
    Validators.required,
    Validators.email,
  ]);

  mobileFormControl = new FormControl('', [
    Validators.required,
    Validators.pattern(/(\(?[0-9]{3}\)?-?\s?[0-9]{3}-?[0-9]{4})/),
  ]);

  constructor(public purchaseService: PurchaseService, private httpClient: HttpClient,
    public itemsService: ItemsService, public vendorService: VendorService,
    public routeParams: ActivatedRoute, notifierService: NotifierService, public router: Router,
    public masterService: MasterService, public dailog: MatDialog) {
    this.notifier = notifierService;
    this.routeParams.params.subscribe(params => {
      this.purchaseId = params.purchaseId;
      this.type = params.type;
    });

  }

  ngOnInit() {
    this.getAllItems();
    this.getReorderDetails();
    this.getAllPurchaseList();
    this.purchase.deliveryTime = new Date();
  }


  getAllItems() {
    this.itemsService.getItems().subscribe((data) => {
      this.itemList = data;
      this.filterItems = this.itemControl.valueChanges.pipe(
        startWith(''),
        map(state => state ? this._filterItem(state) : this.itemList.slice())
      );
    }, (err) => {
      console.log(err);
    });
  }

  private _filterItem(value: string): Items[] {
    const filerValue = value.toLowerCase();
    return this.itemList.filter(items => items.itemName.toLowerCase().indexOf(filerValue) === 0);
  }



  itemSelect(itemName) {
    console.log('in');
    this.itemsService.getItemByName(itemName).subscribe((data) => {
      this.purchase.items = data;
      this.unit = data.unit;
      this.getVendorType(data.itemType);
    }, (error) => {
      console.log(error);
    });
  }

  vendorSelect(vendorName: string) {
    this.vendorService.getVendorName(vendorName).subscribe((data) => {
      console.log('vendor', data);
      this.purchase.vendor = data;
      this.vendorName = data.vendorName;
    }, (error) => {
      console.log(error);
    });
  }

  checkVendor(vendorName) {
    if (this.vendorName !== vendorName) {
      this.purchase.vendor.email = '';
      this.purchase.vendor.mobile = '';
    }
  }

  invoiceSelect(invoiceNo: Number) {
    this.purchaseList.forEach(res => {
      if (invoiceNo === res.invoiceNo) {
        this.stockNo = res.stockNo;
      }
    });
  }

  stockNoSelect(stockNo) {
    this.purchaseList.forEach(res => {
      if (stockNo === res.stockNo) {
        this.invoiceNo = res.invoiceNo;
      }
    });
  }

  getAllPurchaseList() {
    this.purchaseService.getAllPurchaseList().subscribe((datas) => {
      this.purchaseList = datas;
      this.filterInvoice = this.invoiceControl.valueChanges.pipe(
        startWith(''),
        map(state => state ? this._filterInvioce(state) : this.purchaseList.slice())
      );
      this.filterStockNo = this.stockNoControl.valueChanges.pipe(
        startWith(''),
        map(state => state ? this._filterStockNo(state) : this.purchaseList.slice())
      );
    }, (err) => {
      console.log(err);
    });
  }

  private _filterInvioce(value: number): Purchase[] {
    const filterValue = value.toLocaleString();
    return this.purchaseList.filter(data => data.invoiceNo.toLocaleString().indexOf(filterValue) === 0);
  }

  private _filterStockNo(value: number): Purchase[] {
    const filterValue = value.toLocaleString();
    return this.purchaseList.filter(data => data.stockNo.toLocaleString().indexOf(filterValue) === 0);
  }

  savePurchaseitems(exampleForm: NgForm) {
    this.purchase.gstValue = this.purchase.totalAmount * Number(this.purchase.items.gst / 100);
    if (this.purchase.items.itemCategory === 'Gas') {
      this.purchase.weight = (this.purchase.items.perQtyWeight) * (this.purchase.weight);
      this.purchase.items.unit = 'KG';
    }
    let yesterday;
    yesterday = new Date();
    yesterday.setDate(yesterday.getDate() - 1);
    if (this.purchase.deliveryDate <= yesterday && this.purchase._id === '') {
      const matDialogRef = this.dailog.open(StockConfirmationComponent, {
      });
      matDialogRef.afterClosed().subscribe(result => {
        if (result === 'CANCEL') {
          this.purchase.stock = '';
          this.purchase.deliveryDate = undefined;
        } else if (result === 'YES') {
          this.purchase.stock = 'STOCK';
          this.purchaseSave(exampleForm);
        }
      });
    } else {
      this.purchase.stock = '';
      this.purchaseSave(exampleForm);
    }
  }

  purchaseSave(exampleForm) {
    this.purchase.createdBy = localStorage.getItem('user');
    this.purchase.modifiedBy = localStorage.getItem('user');
    this.purchase.orderDate = this.myDate;
    if (this.purchase._id === '') {
      this.purchaseService.savePurchase(this.purchase).subscribe((data) => {
        if (data.message) {
          this.notifier.notify('warning', data.message);
        } else {
          this.notifier.notify('success', 'Saved Successfully');
          exampleForm.reset();
          this.purchase.items._id = '';
          this.purchase.vendor._id = '';
          this.purchase.items.itemName = '';
          this.purchase.vendor.vendorName = '';
          this.purchase.vendor.email = '';
          this.getAllItems();
          this.getAllPurchaseList();
        }

      }, (err) => {
        this.notifier.notify('error', 'Not Save');
      });
    } else {
      this.purchaseService.updatePurchase(this.purchase).subscribe((data) => {
        this.notifier.notify('success', 'Updated Successfully');
        exampleForm.reset();
        this.purchase._id = '';
        this.purchase.items._id = '';
        this.purchase.vendor._id = '';
        this.purchase.items.itemName = '';
        this.purchase.vendor.vendorName = '';
        this.getAllItems();
        this.getAllPurchaseList();
      }, (err) => {
        this.notifier.notify('error', 'Not Update');
      });
    }
  }

  getReorderDetails() {
    if (this.purchaseId === undefined) {
    } else {
      this.purchaseService.reOrder(this.purchaseId).subscribe((data) => {
        this.purchase = data;
        this.getVendorType(data.vendor.vendorType);
        if (this.type === 'reOrder') {
          this.purchase._id = '';
        }
      }, (err) => {
        console.log(err);
      });
    }
  }

  getVendorType(itemType: String) {
    this.vendorService.getVendorType(itemType).subscribe((data) => {
      this.vendorList = data;
      if (this.purchaseId === undefined) {
        data.forEach(res => {
          if (res.preferredType === 'YES') {
            this.vendorSelect(res.vendorName);
          }
        });
      }
      this.filterVendor = this.vendorControl.valueChanges.pipe(
        startWith(''),
        map(state => state ? this._filterVendor(state) : this.vendorList.slice())
      );
    }, (err) => {
      console.log(err);
    });
  }
  private _filterVendor(value: string): Vendor[] {
    const filerValue = value.toLowerCase();
    return this.vendorList.filter(vendor => vendor.vendorName.toLowerCase().indexOf(filerValue) === 0);
  }

  searchPurchase() {
    this.purchase.invoiceNo = this.invoiceNo;
    this.purchase.stockNo = this.stockNo;
    this.purchase.orderDate = this.orderDate;
    this.purchaseService.searchPurchase(this.purchase).subscribe((data) => {
      this.purchase = data;
      this.getVendorType(data.vendor.vendorType);
    }, (err) => {
      console.log(err);
    });
  }

  cancel(exampleForm: NgForm) {
    exampleForm.reset();
    this.purchase._id = '';
    this.purchase.items.itemName = '';
    this.purchase.vendor.vendorName = '';
    this.getAllItems();
    this.getAllPurchaseList();
  }
}
